import { ReqQuery, ResJSError } from "@/api/interface/miniapp/jserror";
import http from "../../index"
import { ResPage } from "../../interface";

// 获取错误列表
export const getJSErrorList = (params: ReqQuery) => {
    return http.get<ResPage<ResJSError>>('miniapp/jserror', params);
};

// 获取错误信息
export const getJSErrorInfo = (id: string) => {
    return http.get<ResJSError>(`miniapp/jserror/${id}`);
};